/**
 * 自定义Mutations
 * @module store/mutations
 *
 * @example 定义mutation方式
 *
 * methodName(state, payload) {
 *    state.moduleName.model = payload
 * }
 *
 */
export default {
    set_SELECT_MENU(state, data) {
        state.SELECT_MENU = data
    },
    set_SECOND_MENU(state,data){
        state.SECOND_MENU=data
    },
    set_ENTER_TIMEOUT_PATH(state,data){
        state.ENTER_TIMEOUT_PATH=data
    },
    set_USER_INFO(state,data){
        state.USER_INFO=data
    }
}
